.Landing {
    .Heading {
        text-align: center;
        color: white;
    }

    .Levels {
        display: flex;
        flex-direction: column;
        height: 80vh;
        overflow-y: auto;
        scrollbar-width: none;
        /* Firefox */
        -ms-overflow-style: none;

        /* Internet Explorer 10+ */
        &::-webkit-scrollbar {
            /* WebKit */
            width: 0;
            height: 0;
        }

        .Level {
            border-radius: 3rem;
            display: inline-block;
            width: 3rem;
            height: 3rem;
            line-height: 3rem;
            margin: 2vh auto;
            font-weight: bold;
            background-color: #888;
            box-shadow: 0 8px 8px -4px #444;

            &::after {
                width: 3.1rem;
                height: 3.1rem;
                background-color: #444;
            }

            &.Unlocked {
                background: rgb(3, 161, 105);
                background: linear-gradient(90deg, rgba(3, 161, 105, 1) 35%, rgba(7, 143, 21, 1) 100%);
                cursor: pointer;
            }
        }
    }
}